article{
    width: 44%; height: 265px;
    box-shadow: 2px 3px 6px #00000055;
    margin: 100px auto;
}
article img{
    height: 100%;
    float: left;
    border: 3px solid #777;
    box-sizing: border-box;
    display: block;
}
article div{
    float: left;
    margin: 20px 30px 0px;
    width: 60%;
}
article div h3{
    font-size: 24px;
    font-weight: 300;
}

article div p{
    font-size: 14px;
    font-weight: 100;
    letter-spacing: 0.3px;
    margin-top: 30px;
}